import React from 'react';

import './jquery-1.7.1.min.js'
import './trello.js'
import {Link} from 'react-router'

class RecipeViewer extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      loadingMsg: "Loading board details...",
      board: {},
      lists: []
    };
  }

  componentDidMount() {

    Trello.get(
      `/boards/${this.props.params.boardid}/lists`, 
      {cards: "open"},
      lists => this.setState({lists: lists, loadingMsg: ""}),
      msg => this.setState({loadingMsg: JSON.stringify(msg)})
    );

  }

  lists() {
    const frag = `/board/${this.props.params.boardid}/recipeviewer`

    return this.state.lists
      .filter(list => /^Recipe/.test(list.name))
      .map(list => <li key={list.id}>
        <Link to={`${frag}/${list.id}`}>{list.name}</Link>
        </li>);
  }

  render() {

    return (
      <div>
        <span>{this.state.loadingMsg}</span>
        <div>
          { 
             this.props.children ||
             <div>
               <h2>Recipes found:</h2>
               {this.lists()}
             </div>
          }
        </div>
      </div>
    );
  }

}

export default RecipeViewer;




