// vim: set sw=2 et :
import React from 'react';

import './jquery-1.7.1.min.js'
import './trello.js'
import {Link} from 'react-router'

class BoardPage extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      loadingMsg: "Loading board details...",
      board: {},
      lists: []
    };
  }

  componentDidMount() {

    Trello.get(
      `/boards/${this.props.params.boardid}`, 
      board => this.setState({board: board, loadingMsg: "Loaded board"}),
      msg => this.setState({loadingMsg: JSON.stringify(msg)})
    );

    Trello.get(
      `/boards/${this.props.params.boardid}/lists`, 
      {cards: "open"},
      lists => this.setState({lists: lists, loadingMsg: "Loaded lists"}),
      msg => this.setState({loadingMsg: JSON.stringify(msg)})
    );

  }

  lists() {
    return this.state.lists.map(list => <div key={list.id}>
      <h2>{list.name}</h2>
      {list.cards.map(card => <li key={card.id}>
        <a href={card.url} title={JSON.stringify(card)}><img src="img/trello.png" style={{height: "1em", width: "1em"}}/></a>{card.name}
      </li>)}
    </div>);
  }

  render() {

    const frag = `/board/${this.props.params.boardid}`

    return (
      <div>
        <Link to="/">Back to list</Link>
        <h2>Board: {this.state.board.name || this.props.params.boardid}</h2>
        <span>{this.state.loadingMsg}</span>
        <div>
          <hr/>
          <Link activeClassName="active" to={`${frag}/cards`}>Cards</Link>
          &nbsp;|&nbsp;
          <Link activeClassName="active" to={`${frag}/recipeviewer`}>RecipeViewer</Link>
          &nbsp;|&nbsp;
          <Link activeClassName="active" to={`${frag}/foodplanner`}>FoodPlanner</Link>
          &nbsp;|&nbsp;
          <Link activeClassName="active" to={`${frag}/shoppinglister`}>ShoppingLister</Link>
          &nbsp;|&nbsp;
          <Link activeClassName="active" to={`${frag}/daymaker`}>DayMaker</Link>
          <hr/>
        </div>
        <div>{this.props.children}</div>
      </div>
    );
  }

}

export default BoardPage;




