
class Recipe {
  constructor(name) {
    this.name = name;
    this.steps = [];
  }

  addStep(name, description) {
    this.steps.push( { name, description, ingredients: [] } )
  }

  addIngredient(name) {
    if(this.steps.length == 0) {
      this.addStep("Step 0");
    }

    this.steps[this.steps.length-1].ingredients.push(name);
  }

  addCard(name, description) {
    if(/^Step /.test(name)) {
      this.addStep(name, description);
    } else {
      this.addIngredient(name);
    }
  }
}

export default Recipe;




