import React from 'react';

import './jquery-1.7.1.min.js'
import './trello.js'
import DTrello from './DTrello.js'
import TrelloSystem from './TrelloSystem.js'
import {Link} from 'react-router'

class FoodPlanner extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      loadingMsg: "Loading lists...",
      ingredientsBoard: null,
      lists: []
    };
  }

  extend(obj, o2) {
    return Object.assign(obj, o2);
  }

  componentDidMount() {

    let sys = new TrelloSystem();
    sys.init()
      .then(() => sys.getBoard(this.props.params.boardid))
      .then(board => sys.getLists(board.id))
      .then(lists =>

            lists.filter(list=>list.name == "config")
              .map(list => list.cards
                .filter(card => card.name="settings")
                .map(card => TrelloSystem.parseProperties(card.desc))
                .reduce(this.extend, {})
              )
              .reduce( this.extend, {})

      )
      .then(settings => this.setState({settings: settings}));

  }

  lists() {
    const frag = `/board/${this.props.params.boardid}/recipeviewer`

    return this.state.lists
      .map(list => <li key={list.id}>
        <Link to={`${frag}/${list.id}`}>{list.name}</Link>
        </li>);
  }

  render() {

    return (
      <div>
        <span>{this.state.loadingMsg}</span>
        <div>
        Settings: {JSON.stringify(this.state.settings)}
        </div>
        <div>
          { 
             this.props.children ||
             <div>
               <h2>Lists found:</h2>
               {this.lists()}
             </div>
          }
        </div>
      </div>
    );
  }

}

export default FoodPlanner;




